import React from "react";
import * as Accordion from "components/accordion";
import {
    accordionData,
    accordionDataGet,
    accordionDataOther,
    accordionDataReturn,
} from "pages/main/sections/faq/accordionData";
import * as Configuration from "modules/api/use-config";
import * as Icon from "./icons";
import { AccordionDataType } from "types";

type ItemType = {
    icon: React.ReactNode;
    title: string;
    data: Array<AccordionDataType>;
};

const data = (config?: Configuration.ConfigurationValue): Array<ItemType> => [
    { icon: <Icon.Star />, title: "General questions", data: accordionData },
    { icon: <Icon.Card />, title: "How to get?", data: accordionDataGet },
    {
        icon: <Icon.CircularArrow />,
        title: "How to return?",
        data: accordionDataReturn,
    },
    {
        icon: <Icon.Question />,
        title: "Other",
        data: accordionDataOther(config),
    },
];

const Item: React.FC<{ item: ItemType }> = ({ item }) => {
    return (
        <>
            <div className="flex mt-20 mobile:mt-[60px] items-center">
                {item.icon}
                <h2 className="title ml-2">{item.title}</h2>
            </div>
            <Accordion.Layout items={item.data} />
        </>
    );
};

export const Layout = () => {
    const config = Configuration.useConfig();
    return (
        <section className="section section-content">
            <div className="wide-container flex-col pb-[120px] mobile:pb-20 pt-10">
                {data(config).map((item) => (
                    <Item key={item.title} item={item} />
                ))}
            </div>
        </section>
    );
};
