import React, { useCallback, useState } from "react";
import { AccordionDataType } from "types";
import { Item } from "./Item";
import styles from "./styles.module.scss";

export const Layout: React.FC<{ items: Array<AccordionDataType> }> = ({
  items,
}) => {
  const [currentIdx, setCurrentIdx] = useState(-1);
  const handleClick = useCallback(
    (index: number) => {
      setCurrentIdx((currentValue) => (currentValue !== index ? index : -1));
    },
    [setCurrentIdx]
  );

  return (
    <div className={styles.accordion}>
      {items.map((item, index) => (
        <Item
          key={index}
          data={item}
          isOpen={index === currentIdx}
          onClick={() => handleClick(index)}
        />
      ))}
    </div>
  );
};
Layout.displayName = "Accordion.Layout";
